import React from 'react';
import Select from 'react-select'

import FORM_FIELDS from '../formFields'

let countriesSelect;

class DynamicForm extends React.Component {

    constructor(props) {
        super(props);
        this.optionsByCountryMap = calculateOptionsByCountry(FORM_FIELDS);
        this.state = { 
            // Initialize with the first field (select country)
            initialSearchFields : [<div key='country' className='formField pure-u-1 pure-u-md-1-2 pure-u-lg-1-3'><label>Country: </label> <Select options={countriesSelect} size="20" onChange={this.onCountryChange} className='selectField'/></div>]
        }
        this.selectedCountry = undefined;
        this.selectedCurrency = undefined;
        this.selectedAccountType = undefined;
        this.selectedProfileType = undefined;
    }

    render() {
        return (
            <form className="pure-form pure-form-aligned">
                <fieldset>
                    <legend>Global details:</legend>
                    {this.state.initialSearchFields}
                </fieldset>
                {this.state.accountFields}
                {this.state.intermediaryAccountFields}
                {this.state.profileFields}
                {this.state.addressFields}
            </form>
        );
    }
    
    /**
     * After selecting country, we'll get the available currencies and display them
     * @param {*} event 
     */
    onCountryChange = (event) => {
        // TODO: maybe get to the next field(s) if there is only one option for the country (if there's such a case)
        this.selectedCountry = event.value;

        // Get the available currencies for that country:
        const currenciesSet = new Set();
        this.optionsByCountryMap.get(this.selectedCountry).forEach(element => currenciesSet.add(element.currency));
        const currencyOptions = [];
        Array.from(currenciesSet.values()).forEach(element => {
            currencyOptions.push({ value: element, label: element });
        });

        // Remove the existing fields if needed (ie select country called for a second time)
        let newInitialSearchFields = this.state.initialSearchFields.concat([]);
        while(newInitialSearchFields.length > 1) {
            newInitialSearchFields.pop();
        }

        // Add the currency field to the form
        newInitialSearchFields.push(
            <div key='currency' className='formField pure-u-1 pure-u-md-1-2 pure-u-lg-1-3'><label>Currency: </label> <Select options={currencyOptions} size="20" onChange={this.onCurrencyChange} className='selectField'/></div>
        );
        this.setState( 
            (prevState, props) => ({initialSearchFields : newInitialSearchFields})
        );
    }

    /**
     * After selecting currency, display the available account types for that country/currency
     * @param {*} event 
     */
    onCurrencyChange = (event) => {
        this.selectedCurrency = event.value;

        // Get the available account types:
        const accountTypesSet = new Set();
        this.optionsByCountryMap.get(this.selectedCountry).filter( element => 
            element.currency === this.selectedCurrency
        ).forEach(element => accountTypesSet.add(element.account_type));

        const accountTypeOptions = [];
        Array.from(accountTypesSet.values()).forEach(element => {
            accountTypeOptions.push({ value: element, label: element });
        });

        // Remove the existing fields if needed (ie select currency called for a second time)
        let newInitialSearchFields = this.state.initialSearchFields.concat([]);
        while(newInitialSearchFields.length > 2) {
            newInitialSearchFields.pop();
        }

        // Add the account type field to the form
        newInitialSearchFields.push(
            <div key='accountType' className='formField pure-u-1 pure-u-md-1-2 pure-u-lg-1-3'><label>Account Type: </label> <Select options={accountTypeOptions} size="20" onChange={this.onAccountTypeChange} className='selectField'/></div>
        );

        this.setState( 
            (prevState, props) => ({initialSearchFields : newInitialSearchFields})
        );
    }

    /**
     * After selecting accountType, display profile type
     */
    onAccountTypeChange = (event) => {
        this.selectedAccountType = event.value;

        // Get the available account types:
        const profileTypesSet = new Set();
        this.optionsByCountryMap.get(this.selectedCountry).filter( element => 
            element.currency === this.selectedCurrency && element.account_type === this.selectedAccountType
        ).forEach(element => profileTypesSet.add(element.profile_type));

        const profileTypeOptions = [];
        Array.from(profileTypesSet.values()).forEach(element => {
            profileTypeOptions.push({ value: element, label: element });
        });

        // Remove the existing fields if needed (ie select currency called for a second time)
        let newInitialSearchFields = this.state.initialSearchFields.concat([]);
        while(newInitialSearchFields.length > 3) {
            newInitialSearchFields.pop();
        }

        // Add the account type field to the form
        newInitialSearchFields.push(
            <div key='profileType' className='formField pure-u-1 pure-u-md-1-2 pure-u-lg-1-3'><label>Profile Type: </label> <Select options={profileTypeOptions} size="20" onChange={this.onProfileTypeChange} className='selectField'/></div>
        );

        this.setState( 
            (prevState, props) => ({initialSearchFields : newInitialSearchFields})
        );
    }

    /**
     * After selecting profile type, get the form for that combination and display.
     */
    onProfileTypeChange = (event) => {
        this.selectedProfileType = event.value;

        const selectedForm = FORM_FIELDS.filter( element => 
            element.country === this.selectedCountry &&
            element.currency === this.selectedCurrency &&
            element.account_type === this.selectedAccountType &&
            element.profile_type === this.selectedProfileType
        )[0]; // Should only be 1 element

        const accountFields = createFieldSet(selectedForm.account);
        const intermediaryAccountFields = createFieldSet(selectedForm.intermediary_account);
        const profileFields = createFieldSet(selectedForm.profile);
        const addressFields = createFieldSet(selectedForm.address);

        let initialSearchFieldsClone = this.state.initialSearchFields.concat([]);
        this.setState( 
            (prevState, props) => (
                {
                    initialSearchFields : initialSearchFieldsClone,
                    accountFields,
                    intermediaryAccountFields,
                    profileFields,
                    addressFields
            })
        );
    }
}

/**
 * returns A map of the form key: country and value: array of options is an object with currency, account_type, profile_type
 */
function calculateOptionsByCountry(rawFields) {
    const countriesMap = new Map();
    countriesSelect = [];
    rawFields.forEach(element => {
        if (countriesMap.has(element.country)) {
            countriesMap.get(element.country).push(element);
        } else {
            countriesMap.set(element.country, [element]);
            countriesSelect.push({ value: element.country, label: element.country })
        }
    });
    return countriesMap;
}

/**
 * Creates a fieldset with legend and inputs defined in rawfield
 * @param {name, {fields[]} rawFields 
 */
function createFieldSet(rawFields){
    const fieldSetTitle = rawFields.name;

    const fields = [];
    rawFields.fields.forEach(rawField => {
        fields.push(createInputField(rawField));
    });

    return (
        <fieldset>
            <legend>{fieldSetTitle}</legend>
            <div className='pure-g'>
            {fields}
            </div>
        </fieldset>
    )
}

/**
 * Takes a raw object representing an input field and return the jsx of the field
 * @param {*} rawField eg:{
            "name": "bankId",
            "fieldType": "text",
            "displayLabel": "BIC/SWIFT"
          }
 */
function createInputField(rawField){
    let input;
    switch(rawField.fieldType){
        case 'tel':
            input = <input type='tel' name='{rawField.name}'/>
        break;
        default://Input type text
            input = <input type='text' name='{rawField.name}'/>
        break;
    }

    return (
    <div key={rawField.name} className='formField pure-u-1 pure-u-md-1-2 pure-u-lg-1-3'>
        <label>{rawField.displayLabel}: </label>
        <br />
        {input}
    </div>
    );
}

export default DynamicForm;